
@use "sass:math";
@use "@/assets/scss/_base";

:deep(.main) {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.fps,
.chart-title {
    position: fixed;
    z-index: 1;
    top: 0;
    background: v-bind("moduleConfig.stageBackgroundColor");

    @include base.column-padding;
}

.fps {
    left: 0;
}

$width: 80;
$height: 80;

.stage-container {
    display: flex;
    width: $width * 1vw;
    width: $width * 1svw;
    min-width: calc(v-bind("moduleConfig.mainLayer.noAircraftsMessage.width") * 1px);
    height: $height * 1vh;
    height: $height * 1svh;
    min-height: calc(v-bind("moduleConfig.mainLayer.noAircraftsMessage.height") * 1px);
    align-items: center;
    justify-content: center;
}
